* {
    padding: 0;
    margin: 0;
}

::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

::-webkit-scrollbar-thumb {
    background-color: rgb(77, 76, 76);
    border-radius: 9em;
}

.main {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0 auto;
}

.app {
    position: relative;
    width: 100%;
    overflow: hidden;
}

.app-inner {
    height: 100%;
    width: 100%;
    overflow-y: auto;
}

@media (max-width: 560px) {
    ::-webkit-scrollbar {
        width: 1px;
        height: 1px;
    }
}